@mixin box-height($name, $lineNum) {
  .#{$name} {
    display: block;
    line-height: 18px;
    max-height: ($line-height-computed * $lineNum);
    .additional-text {
      display: none;
    }
    &.expanded-view {
      height: auto;
      max-height: inherit; // reset max height
      .ellipsis {
        display: none;
      }
      .additional-text {
        display: inline;
      }
    }
  }
}

@mixin show-list-item($name, $listItemNum, $listItemHide) {
  ul.#{$name} {
    li:nth-child(#{$listItemNum}):after {
      content: '... ';
    }
    li:nth-child(n + #{$listItemHide}) {
      display: none;
    }
    &.expanded-view {
      li:nth-child(#{$listItemNum}):after {
        content: '';
      }
      li:nth-child(n + #{$listItemHide}) {
        display: inline;
      }
    }
  }
}

