.timeline {
  position: relative;
  margin: 0 0 50px 0;
  padding: 0;
  list-style: none;
}

.timeline:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 4px;
  background: #ddd;
  left: 31px;
  margin: 0;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}

.timeline > li {
  position: relative;
  margin-right: 10px;
  margin-bottom: 15px;
}

.timeline > li::after {
  clear: both;
  content: '';
  display: table;
}

.timeline > li > .timeline-item {
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  margin-top: 0;
  background: #fff;
  color: #444;
  margin-left: 60px;
  margin-right: 15px;
  padding: 0;
  position: relative;
}

.timeline > li > .timeline-item > .time {
  float: right;
  padding: 10px;
  font-size: 14px;
}

.timeline > li > .timeline-item > .timeline-header {
  margin: 0;
  border-bottom: 1px solid #f4f4f4;
  padding: 10px;
  font-size: 13px;
  line-height: 1.1;
}

.timeline > li > .timeline-item > .timeline-header > a {
  font-weight: 600;
}

.timeline > li > .timeline-item > .timeline-body, .timeline > li > .timeline-item > .timeline-footer {
  padding: 10px;
}

.timeline > li > .fa, .ui-datepicker .ui-datepicker-prev .timeline > li > .ui-icon:after, .ui-datepicker .ui-datepicker-next .timeline > li > .ui-icon:after, .ui-datepicker .timeline > li > .ui-datepicker-prev, .ui-datepicker .timeline > li > .ui-datepicker-next, .alert .timeline > li > .alert-icon,
.timeline > li > .glyphicon,
